<template>
  <header class="nav-bar">
    <el-row>
      <el-col :span="16">
        <ul class="nav-bar-left">
          <li @click="goHome">
            <div>
              <span style="font-family: 'FangSong';">记录见证成长</span>
              <div style="font-size:12px">It's the loneliest feeling not to know who you are.</div>
            </div>
          </li>
        </ul>
      </el-col>
      <el-col :span="8">
        <ul class="nav-bar-right">
          <li v-for="(item,index) in menuItems" :key="index" @click="activeIndex = index">
            <router-link :to="item.to">
              <i class="iconfont" :class="item.icon"></i>
              <sub :class="{active:activeIndex==index}">&nbsp;</sub>{{item.name}}
            </router-link>
          </li>
        </ul>
      </el-col>
    </el-row>
  </header>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '0',
        menuItems:[{
          to:'/edit',
          name:'笔记',
          icon:'icon-caidan'
        },{
          to:'/edit/chart',
          name:'统计',
          icon:'icon-tongji'
        },{
          to:'/edit/lab',
          name:'实验室',
          icon:'icon-tongji'
        }]
      };
    },
    methods:{
      goHome(){
        this.$router.push({path: '/read2'})
      }
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  width: 100%;
  box-sizing: border-box;
  ul{
    margin: 0;
  }
  li{
    height: 60px;
    
    box-sizing: border-box;
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
    cursor: pointer;
    transition: all .28s;
  }
  ul:after{
    content: ".";
    clear:both;
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  .nav-bar-left{
    li div:first-child{
      transform: translateY(15px);
    }
    li:hover{
      animation:sucaijiayuan 1s .1s ease both;
      -webkit-animation:sucaijiayuan 1s .1s ease both;
      -moz-animation:sucaijiayuan 1s .1s ease both;
    }
    @-webkit-keyframes sucaijiayuan {
      0% {
        -webkit-transform:scale(1)
      }
      10%,20% {
        -webkit-transform:scale(0.8) rotate(-2deg)
      }
      30%,50%,70%,90% {
        -webkit-transform:scale(1.1) rotate(2deg)
      }
      40%,60%,80% {
        -webkit-transform:scale(1.1) rotate(-2deg)
      }
      100% {
        -webkit-transform:scale(1) rotate(0)
      }
    }
  }
  .nav-bar-right{
    position: relative;
    li{
      position: relative;
      line-height: 60px;
      a{
        color: inherit;
      }
      sub{
        width: 0;
        height: 0;
        position: absolute;
        bottom:0;
        left: 50%;
        opacity: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-bottom: 10px solid #fff;
      }
    }
    sub.active{
      opacity: 1;
    }
    li:hover{
      sub{
        opacity: 1;
      }
    }
    &_div{
      position: absolute;
      width: 150px;
      top: 60px;
      right: 75px;
      transition: all .2s;
      border:1px solid #e0e0e0;
      background-color: rgba(0,0,0,.1);
      z-index: 99;
      div{
        display: inline-block;
        width: 33.33%;
        height: 33px;
        cursor: pointer;
        transition: all .2s;
      }
      div:hover{
        transform: scale(1.1);
      }
    }
  }
}
</style>


