<template>
  <div class="edit">
    <!-- <save-panel :show="showPanel" @on-click-hide="hidePanel"/> -->
    <el-row style="height:100%;">
      <div class="edit-left">
          <el-menu
            default-active="1"
            background-color="#fff"
            text-color="#000000"
            active-text-color="#000">
            <el-submenu index="1">
              <template slot="title">
                <i class="iconfont icon-color" :class="getClass('005')"></i>
                <span>笔记栏</span>
              </template>
              <el-menu-item v-for="(item,index) in notesList" :index="index+'key'" :key="index" @click="queryNotes(item.url,index)" >
                {{item.name}}
              </el-menu-item>
            </el-submenu>
          </el-menu>
      </div>
      <transition name="slide-left">
        <el-col v-show="markdownValue" :span="20" :offset="4" class="edit-right">
          <mavon-editor :value="markdownValue" @save="onSave" style="height:100%"/>
        </el-col>
      </transition>
    </el-row>
  </div>
</template>

<script>
import SavePanel from './SavePanel.vue'
export default {
  components: {
    SavePanel
  },
  data () {
    return {
      markdownValue:'',
      activeIndex: 0,
      chooseUrl: '',
      showPanel: false,
      notesList:[]
    }
  },
  methods:{
    getClass(tagid){
      switch (tagid){
        case "001":
          return "icon-vuejs"
        case "002":
          return "icon-nodejs"
        case "003":
          return "icon-logo-javascript"
        case "004":
          return "icon-angular"
        default:
          return "icon-qita"
      }
    },
    onSave(value,render){
      if(this.chooseUrl){
        if(value||this.markdownValue){
          this.$http.post('/edit/saveNotes.next',{markdownValue:value||this.markdownValue,render:render,url:this.chooseUrl}).then((res)=>{
            this.$message({
              message: 'save success',
              type: 'success'
            });
          },(err)=>{
            alert(err)
          })
        }
      }
    },
    queryNotes(url,index){
      if(url){
        this.activeIndex = index
        this.chooseUrl = url
        this.markdownValue = ""
        this.$http.post('/edit/queryNotes.next',{url:url}).then((res)=>{
          if(res){
            this.markdownValue = res.data.markdownValue
          }
        },(err)=>{
        })
      }
    },
    queryNotesAll(){
      this.$http.post('/edit/queryNotesAll.next').then(res=>{
        this.notesList = res.data
        this.queryNotes(res.data[0].url,0)
      })
    },
    hidePanel(){
      this.queryNotesAll()
      this.showPanel = false
    }
  },
  created() {
    this.queryNotesAll()
  }
}
</script>


<style scoped lang="scss">
.edit{
  height: calc(100% - 60px);
  width: 100%;
  &-left{
    height: 100%;
    position: absolute;
    z-index: 1998;
    min-width: 210px;
    background-color: #f1f1f1;
    overflow-y: scroll;
  }
  &-right{
    transition: all .28s;
    height:100%;
    overflow: scroll;
  }
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
</style>


