<template>
  <div class="full" v-show="show" @click="hide">
    
    <div class="full-content">
      <div v-html="htmlText" class="markdown-body"></div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'full',
  componentName: 'full',
  props:{
    show: Boolean
  },
  data () {
    return {
      htmlText: String
    }
  },
  methods:{
    hide(){
      this.show = false
    },
    queryNotesHtml(url){
      url = url||'/notes/default'
      this.$http.post('/edit/queryNotes.next',{url:url}).then((res)=>{
        if(res){
          this.$nextTick(function () {
            this.htmlText = res.data.render
          })
        }
      },(err)=>{
      })
    }
  },
  mounted(){
    this.queryNotesHtml()
  }
}
</script>
<style scoped lang="scss">
.full{
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0px;
  z-index: 9;
  background-color: rgba(0,0,0,.3);
  &-content{
    max-width: 80%;
    margin: auto;
    height: 100%;
    overflow: scroll;
    background-color: #fff;
    padding: 10px;
  }
}
</style>


