<template>
  <div class="home">
    <span style="font-family: 'FangSong';">假前端的自我修养</span>
    <div style="font-size:12px;margin-bottom:40px;">It's the loneliest feeling not to know who you are.</div>

    <el-carousel :interval="5000" height="230px" type="card">
      <el-carousel-item v-for="(item,index) in cardItem" :key="index">
        <div class="home-card-title">
          {{item.name}}
        </div>
        <div class="home-card-mark" @click="goRouter(item)">
          {{item.mark}}
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  components: {
  },
  data () {
    return {
      cardItem:[{
        name:'易笔记',
        to:'/read2',
        mark:'这是一个完整项目,Vue2.0+Koa2搭建;用作文档编辑和浏览,记录日常开发文档和笔记',
        url:'https://xhexu.github.io/#/read'
      },{
        name:'易笔记服务端',
        to:'/koa',
        mark:'学习Koa框架的使用',
        url:'https://github.com/xhexu/EasyNotesServer'
      },{
        name:'智慧园区',
        to:'/vuetool',
        mark:'合作项目',
        url:'https://github.com/xhexu/smartPack'
      },{
        name:'混合开发',
        to:'/app',
        mark:'混合开发App技术,也是工作三年以来一直从事的工作内容',
        url:'https://github.com/xhexu/RubikCube'
      }]
    }
  },
  methods:{
    goRouter(item){
      if(item.name="易笔记"){
        this.$router.push({path: item.to})
      }else{
        window.location.href = item.url
      }
    }
  }
}
</script>


<style scoped lang="scss">
body{
  height: inherit;
}
.home{
  height: 100%;
  margin: 0 auto;
  max-width: 960px;
  justify-content: center;
  position: relative;
  padding-top: 50px;
  box-sizing: border-box;
  .el-carousel__item{
    background: url('../../static/001.jpg') no-repeat;
    background-size: cover;
  }
  &-card{
    
    &-title{
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-mark{
      line-height: normal;
      position: absolute;
      color: #fff;
      position: absolute;
      padding: 10px;
      box-sizing: border-box;
      font-size: 12px;
      bottom: 0;
      transition:all .5s ease-in;
      opacity: 0;
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-mark:hover{
      opacity: 1;
      color: #000;
      background: url('../../static/00.jpg') no-repeat;
      background-size: cover;
    }
  }
}
</style>


