<template>
  <el-row class="h-lab">
    <el-col :span="4">
        <el-button @click="start()">开始</el-button>
    </el-col>
    <el-col :span="20">
      <div style="height:50%;width:inherit">
        <div style="float:left;width:50%">  
          <canvas id="cvs" width="500" height="500"></canvas>
        </div>
        <div style="float:left;width:50%">

        </div>
        
      </div>
      <div style="height:50%;width:100%">

      </div>
    </el-col>
  </el-row>
 
</template>

<script>
export default {
  name:'Lab',
  data () {
    return {
     
    }
  },
  methods:{
    start(){
      var index = 0,speed = 500;
      var cvs = document.getElementById('cvs');
      var ctx = cvs.getContext('2d');
      var lists = [32, 31, 29, 26, 20, 24, 35, 6, 25, 39, 13,
        33, 8, 38, 10, 18, 5, 3, 27, 21, 17, 34, 28, 14, 
        12, 23, 7, 22, 16, 19, 37, 9, 2, 30, 11, 36, 4, 
        15, 40, 1];

      
      function clearCanvas(){
        ctx.clearRect(0,0,cvs.clientWidth,cvs.height);
      }

      function draw(arr, first, second){
        clearCanvas();
        for(var i in arr){
          var x = 10+12*i;
          var y = arr[i]*10;
          ctx.beginPath();
          if(i == first)
            ctx.fillStyle = 'yellow';
          else if(i == second){
            ctx.fillStyle = 'yellow';
          }else{
            ctx.fillStyle = 'orange';
          }
          ctx.fillRect(x,480-y,10,y);
          ctx.closePath();
        }
      }

      function cpy(arr){
        var item = [];
        for(var i in arr){
            item[i] = arr[i];
        }
        return item;
      }

      function quickSort(items, left, right) {
          var index;
          if (items.length > 1) {
              index = partition(items, left, right);
              if (left < index - 1) {
                  quickSort(items, left, index - 1);
              }
              if (index < right) {
                  quickSort(items, index, right);
              }

          }
          return items;
      }

      function partition(items, left, right) {
        var pivot = items[Math.floor((right + left) / 2)],
            i = left,
            j = right;
        while (i <= j) {
            while (items[i] < pivot) {
                i++;
            }
            while (items[j] > pivot) {
                j--;
            }
            if (i <= j) {
                swap(items, i, j);
                i++;
                j--;
            }
        }
        return i;
      }

      function swap(items, firstIndex, secondIndex){
        //交换数据
        var temp = items[firstIndex];
        items[firstIndex] = items[secondIndex];
        items[secondIndex] = temp;
        //延时画图
        var ls = cpy(items);
        setTimeout(function(){
            draw(ls, firstIndex, secondIndex);
        }, index * speed);
        index ++;
      }


      function doStart(){
        draw(lists);
        var arr = cpy(lists);   //复制数组，下次可以重新开始动画
        index = 0;              //初始化延时计数
        quickSort( arr, 0, arr.length - 1 );    //执行快排
        setTimeout(function(){  //结束绘图
            draw(arr);
        }, index * speed);
      }

      doStart()
    }
  }
}
</script>


<style scoped lang="scss">
.h-lab{
  height: calc(100% - 60px);
}
</style>


