<template>
  <div class="app" :class="[theme]"> 
    <router-view/>
  </div>
</template>
<script>
import { mapMutations, mapState,mapGetters } from 'vuex';
export default {
  name: 'App',
  components: {
  },
  data () {
    return {
    }
  },
  computed:{//计算属性
    ...mapState(['theme']),
    ...mapGetters(['getTheme'])
  },
  mounted(){
    console.log(this.getTheme)
  }
}
</script>

<style lang="scss">
@import '../static/base.scss';
@import '../static/theme.scss';
.app{
  height: 100%;
  width: 100%;
}
</style>



