<template>
  <div class="edit-chart">
    <div id="myChart"></div>
  </div>
</template>

<script>
import Chart from './chart.js'
export default {
  data () {
    return {
    }
  },
  methods:{
    queryChartData(){
      this.$http.post('/chart/queryChart.next',{type:'bar'}).then(res=>{
        if(res.success){
          Chart.drawBar(document.getElementById('myChart'),{
            title:'笔记数量统计',
            xAxis:res.data.xAxis,
            data: res.data.data
          })
        }
      })
    }
  },
  mounted() {
    this.queryChartData() 
  }
}
</script>


<style scoped lang="scss">
.edit-chart{
  height: calc(100% - 60px);
  width: 100%;
  #myChart{
    height: 100%;
    box-sizing: border-box;
    background-color: rgba(255,255,255,1);
  }
}
</style>


