import Echarts from 'echarts'

export default {
  drawBar(dom,options){
    let myChart = Echarts.init(dom)
    
    myChart.setOption({
        title: { text: options.title },
        tooltip: {},
        xAxis: {
            data: options.xAxis
        },
        yAxis: {},
        series: [{
            type: 'bar',
            data: options.data
        }]
    })
  }
}


