const localEvent = function (item) {
  this.get = function () {
      return localStorage.getItem(item) ? JSON.parse(localStorage.getItem(item)) : '';
  }
  this.set = function (obj) {
      localStorage.setItem(item, JSON.stringify(obj));
  }
  this.clear = function () {
      localStorage.removeItem(item);
  }
}
export default{
  _tanArryToDouble: function(ary,len){
    let newAry = []
    if(ary instanceof Array){
      len = len||3
      let line = ary.length
      let count = line%len === 0?(line/len):(Math.floor(line/len)+1)
      for(let i=0;i<count;i++){
        let temp = ary.slice(i*len,i*len+len)
        newAry.push(JSON.parse(JSON.stringify(temp)))
      }
    }
    return newAry
  },
  _scrollDownOrUp(fn){
    window.onmousewheel = document.onmousewheel = function(e){
      var scrollTop=document.documentElement.scrollTop||document.body.scrollTop;
      e = e || window.event
        if(e.wheelDelta){//IE/Opera/Chrome
          console.log(e.wheelDelta)
          console.log("123",scrollTop)
          fn&&fn(e.wheelDelta<0&&screenTop>=500) 
        }else if(e.detail){//Firefox
          fn&&fn(e.detail>0) 
        } 
    }
  },
  _formatDate(time){
    return new Date(time).toLocaleString().substr(0,16)
  },
  _localTheme(){
    return new localEvent('lx_theme')
  },
  _getPageScroll() {
    let xScroll, yScroll;
    if (self.pageYOffset) {
        yScroll = self.pageYOffset;
        xScroll = self.pageXOffset;
    } else if (document.documentElement && document.documentElement.scrollTop) {     // Explorer 6 Strict
        yScroll = document.documentElement.scrollTop;
        xScroll = document.documentElement.scrollLeft;
    } else if (document.body) {// all other Explorers
        yScroll = document.body.scrollTop;
        xScroll = document.body.scrollLeft;
    }
    return new Array(xScroll,yScroll);
  }
}


