<template>
  <header class="nav-bar" :style="{ position: position,zIndex:'100'}">
    <el-row>
      <el-col :span="16">
        <ul class="nav-bar-left">
          <li @click="goHome">
            <span style="font-family: 'FangSong';">假前端的自我修养</span>
          </li>
        </ul>
      </el-col>
      <el-col :span="8">
        <ul class="nav-bar-right">
          <li>
            <router-link to="/read2">
              <i class="iconfont icon-bangzhuzhongxin"></i>
              <sub>&nbsp;</sub>
              笔记中心
            </router-link>
          </li>
          <li>
            <router-link to="/edit">
              <i class="iconfont icon-console"></i>
              <sub>&nbsp;</sub>
              写文章
            </router-link>
          </li>
          <li @click="showTheme=!showTheme">
            <i class="iconfont icon-qiehuan"></i>
          </li>
          <div class="nav-bar-right_div" v-show="showTheme">
            <div v-for="(item,index) in themeColor" @click="switchTheme(item)" :key="index" :style="{'background-color':item.color}">
            </div>
          </div>
        </ul>
      </el-col>
    </el-row>
  </header>
</template>
<script>
  export default {
    props:{
      position: {
        type: String,
        default: 'static'
      }
    },
    data() {
      return {
        showTheme: false,
        themeColor:[{
          theme:'theme_blue',
          color:'rgba(64, 158, 255, .9)'
        },{
          theme:'theme_green',
          color:'#00d1b2'
        },{
          theme:'theme_pink',
          color:'#f39894'
        },{
          theme:'theme_orange',
          color:'#f4b976'
        },{
          theme:'theme_cyan',
          color:'#26b6be'
        },{
          theme: 'theme_black',
          color:'#545c64'
        }]
      };
    },
    beforeRouteUpdate (to, from, next) {
      console.log(1232)
      next()
    },
    methods:{
      goHome(){
        this.$router.push({name: 'Home'})
      },
      switchTheme(theme){
        this.showTheme = false
        this.$store.dispatch("switchTheme",theme.theme)
      }
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  width: 100%;
  box-sizing: border-box;
  ul{
    margin: 0;
  }
  li{
    height: 60px;
    line-height: 60px;
    box-sizing: border-box;
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
    cursor: pointer;
    transition: all .28s;
  }
  ul:after{
    content: ".";
    clear:both;
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  .nav-bar-left{
    li:hover{
      animation:sucaijiayuan 1s .1s ease both;
      -webkit-animation:sucaijiayuan 1s .1s ease both;
      -moz-animation:sucaijiayuan 1s .1s ease both;
    }
    @-webkit-keyframes sucaijiayuan {
      0% {
        -webkit-transform:scale(1)
      }
      10%,20% {
        -webkit-transform:scale(0.8) rotate(-2deg)
      }
      30%,50%,70%,90% {
        -webkit-transform:scale(1.1) rotate(2deg)
      }
      40%,60%,80% {
        -webkit-transform:scale(1.1) rotate(-2deg)
      }
      100% {
        -webkit-transform:scale(1) rotate(0)
      }
    }
  }
  .nav-bar-right{
    position: relative;
    li{
      position: relative;
      a{
        color: inherit;
      }
      sub{
        width: 0;
        height: 0;
        position: absolute;
        bottom:0;
        left: 50%;
        opacity: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-bottom: 10px solid #fff;
      }
    }
    sub.active{
      opacity: 1;
    }
    li:hover{
      sub{
        opacity: 1;
      }
    }
    &_div{
      position: absolute;
      width: 150px;
      top: 60px;
      right: 75px;
      transition: all .2s;
      border:1px solid #e0e0e0;
      background-color: rgba(0,0,0,.1);
      z-index: 99;
      div{
        display: inline-block;
        width: 33.33%;
        height: 33px;
        cursor: pointer;
        transition: all .2s;
      }
      div:hover{
        transform: scale(1.1);
      }
    }
  }
}
</style>


