<template>
  <div style="height:100%;background-color:#f5efef">
    <div class="koa" @click="goHome">
      EasyNotes
    </div>
     <el-row :gutter="5" style="height:calc(100% - 50px);">
       <el-col :span="4" style="height:100%">
         <div style="height:100%;overflow-y: scroll;position: relative;z-index:2">
            <div class="koa-menu over_text" v-for="(item,index) in menuFiles" :key="index" @click="chooseItem(index)">
              {{item.name}}
            </div>
         </div>
       </el-col>
       <el-col :span="20" style="height: calc(100% - 31px);">
         <div class="koa-tabs" id="test-001">
           <div class="over_text" @click="itemClick(index)" :class="{'item-active':activeIndex==index}" v-for="(item,index) in tabsItem" :key="index">
             <span>{{item.name}}</span>
             <i class="iconfont icon-guanbi" @click="removeItem(index)"></i>
           </div>
         </div>
         <div class="koa-content">
           <pre v-if="menuText">{{menuText}}</pre>
         </div>
       </el-col>
     </el-row>
  </div>
</template>

<script>
export default {
  name: 'Koa',
  data () {
    return {
      menuFiles:[],
      tabsItem:[],
      activeIndex: -1,
      menuText:''
    }
  },
  methods: {
    goHome(){
      this.$router.push({name: 'Home'})
    },
    chooseItem(index){
      const _index = this.tabsItem.findIndex((v)=>{
        return this.menuFiles[index].url == v.url
      })
      if(_index==-1){
        this.tabsItem.push(this.menuFiles[index])
        this.activeIndex = this.tabsItem.length-1
        const dom = document.getElementById('test-001')
        if(dom.scrollWidth>dom.offsetWidth){
          let scrollValue = (dom.offsetWidth-dom.scrollWidth)-100
          dom.style.transform = `translateX(${scrollValue}px)`
        }
      }else{
        this.activeIndex = _index
      }
      if(this.menuFiles[index]){
        this.fetchFile(this.menuFiles[index].url)
      }
    },
    removeItem(index){
      this.tabsItem.splice(index,1)
      this.activeIndex = this.tabsItem.length-1
      index!=0?this.fetchFile(this.tabsItem[this.activeIndex].url):this.menuText = ''
      const dom = document.getElementById('test-001')
      dom.style.transform = `translateX(${-111}px)`
    },
    itemClick(index){
      this.activeIndex = index
      this.fetchFile(this.tabsItem[index].url)
    },
    fetchServerFiles(){
      this.$http.post('/edit/queryFiles.next',{isLoading:false}).then((res)=>{
        if(res){
          this.menuFiles = res.data
          this.chooseItem(0)
        }
      },(err)=>{
      })
    },
    fetchFile(url){
       this.$http.post('/edit/readFiles.next',{url:url},{isLoading:false}).then((res)=>{
        if(res){
          this.menuText = res.data
        }
      },(err)=>{
      })
      
    },
    doFetch(){
      this.fetchServerFiles()
    }
  },
  mounted() {
    this.doFetch() 
  }
}
</script>

<style scoped lang="scss">

.koa{
  height: 60px;
  width: 100%;
  line-height: 60px;
  padding-left: 20px;
  box-sizing: border-box;
  border-bottom:1px solid rgba(159,234,249,.3);
  background-color: rgb(84, 92, 100);
  color:#fff;
  position: relative;
  z-index: 3;
  margin-bottom: 5x;
  cursor: pointer;
  &-menu{
    padding: 10px 10px;
    font-size: 12px;
    cursor: pointer;
    border-bottom:1px solid rgba(159,234,249,.3);
    background-color: #fff;
  }
  &-content{
    width:100%;
    height:100%;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: #fff;
    border-left:1px solid #f1f1f1;
    pre{
      margin: 0;
      padding: 10px;
      min-height: 100%;
      overflow: auto;
      white-space: pre-wrap;
      word-wrap: break-word;
    }
  }
  &-tabs{
    height:30px;
    width: 100%;
    border-bottom:1px solid rgba(159,234,249,.5);
    white-space: nowrap;
    transition: all .5s ease-in-out;
    transform: translateX(0);
    position: relative;
    z-index:1;
    background-color: #fff;
    .item-active{
      background-color: #fff;
      color:red;
    }
    div{
      display: inline-flex;
      align-items: center;
      font-size: 12px;
      min-width: 100px;
      max-width: 200px;
      text-align: center;
      border-right: 1px solid #c9c9c9;
      border-bottom: none;
      height: 100%;
      padding: 0 5px;
      cursor: pointer;
      span{
        min-width: 80%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        transition: all .5s ease-in-out;
        transform: translateX(0px);
      }
      i{
        transition: all .5s ease-in-out;
        transform: translateX(50px);
      }
    }
    div:hover{
      i{
        transform: translateX(5px);
      }
      span{
        transform: translateX(5px);
      }
    }
  }
}
</style>



