import Vue from 'vue'
import Vuex from 'vuex'
import utils from '../utils/utils'

Vue.use(Vuex)
const store = new Vuex.Store({
  state:{
    theme:utils._localTheme().get().theme||'theme_blue',
  },
  getters:{
    getTheme: state=>{
      return state.theme
    }
  },
  mutations:{
    chageTheme:(state,obj)=>{
      state.theme = obj.theme
      utils._localTheme().set(obj)
    }
  },
  actions: {
    switchTheme (context,params) {
      context.commit('chageTheme',{theme:params})
    }
  }
})

export default store


