<template>
  <li class="tree-node">
    <div v-for="(item,index) in model" :key="index">
      <div
        class="tree-node_item"
        draggable="true" 
        @dragend="ondragend(index)" 
        @dragover="ondragover(index)" 
        @drag="ondrag(index)"
        @drop="ondrop(index)" 
        @dragstart="ondragstart(index)"
        @click="toggle(item)"
        @contextmenu="showMenu(item)">
        <i v-if="isFolder(item)" class="iconfont icon-color" :class="{'icon-wenjianjia':!item.open,'icon-folder':item.open}"></i>
        <i v-if="!isFolder(item)" class="iconfont icon-color icon-ruanjiankaifabao"></i>
        <!-- <i v-if="isFolder(item)" class="iconfont icon-color" :class="{'icon-add':!item.open,'icon-jian':item.open}"></i> -->
        <span @dblclick.stop="goEdit(item)">{{ item.name }}</span>
      </div>
      <ul class="tree-node_children" v-if="item.open">
        <tree-t
          v-for="(model, index) in item.children"
          :onChoose="onChoose"
          :key="index"
          :model="[model]"/>
      </ul>
    </div>
  </li>
</template>
<script>
  export default {
    name: 'treeT',
    componentName: 'treeT',
    props: {
      model: Array,
      onChoose: Function
    },
    data: function () {
      return {
      }
    },
    computed: {
     
    },
    mounted(){
      
    },
    methods: {
      isFolder: function (item) {
        return item.children &&
          item.children.length>=0
      },
      toggle: function (item) {
        event.preventDefault();
        this.onChoose(item)
        if (this.isFolder(item)) {
          if(item.hasOwnProperty('open')){
            item.open = !item.open
          }else{
            this.$set(item,'open',true)
          }
        }
      },
      showMenu: function(item){
        
      },
      goEdit(item){
        //命名路由跳转,传递参数使用params
        if(!this.isFolder(item)){
          this.$router.push({name: 'Edit',params:item})
        }
      },
      ondragstart(index){
        event.dataTransfer.setData("item",'你好');
        console.log('ondragstart')
      },
      ondrop(index){
        console.log('ondrop',event.dataTransfer.getData("item"))
      },
      ondrag(index){
        console.log('ondrag')
      },
      ondragover(index){
        event.preventDefault();
        console.log('ondragover34')
      },
      ondragend(ev){
        console.log('ondragend')
      }
    }
  }
</script>
<style lang="scss" scoped>
.tree-node{
  list-style: none;
  .icon-color{
    color: inherit;
  }
  &_item{
    font-size: 14px;
    cursor: pointer;
  }
  &_children{
    margin: 0;
    padding-left: 1em;
    list-style-type: dot;
  }
}
</style>


